// Carrega os conteúdos na página
import './style.css';
import priTi_mp4 from './videos/priTi.mp4';
import casa from './images/casa.png';
import ferramentas from './images/ferramentas.png';
import qrcode from './images/qrcode.png';

// Insere favicon
const linkFaviconTag = document.createElement('link');
linkFaviconTag.type = 'image/png';
linkFaviconTag.rel = 'shortcut icon';
linkFaviconTag.href = casa;

document.getElementsByTagName('head')[0].appendChild(linkFaviconTag);

// Insere as imagens
document.getElementById("priTi_mp4").src = priTi_mp4;
document.getElementById("casa").src = casa;
document.getElementById("ferramentas").src = ferramentas;
document.getElementById("img-qrcode").src = qrcode;